@import '../../mixin'; // responsive web

.listItem{
    width: 225px;
    height: 120px;
    background-color: var(--main-color);
    margin-right: 5px;
    overflow:hidden;
    cursor:pointer;
    color: #fff;
    img{
        width:100%;
        height: 100%;
        object-fit: cover;
    }
    .video{
        width:100%;
        height:140px;
        object-fit: cover;
        position:absolute;
        top: 0;
        left: 0;
    }
}

@media (hover: hover) and (pointer: fine){
	.listItem{
        &:hover{
            a{
            color: #fff;
            text-decoration: none;
            }
            width: 325px;
            height: 300px;
            position: absolute;
            top: -150px;
            -webkit-box-shadow: 0px 2px 10px -1px rgba(255,255,255,0.2); 
            box-shadow: 0px 2px 10px -1px rgba(255,255,255,0.2);
            border-radius: 5px;
            img{ // hover 시 이미지 크기 변경
                height: 140px;
            }
            .itemInfo{
                display: flex;
                flex-direction: column;
                padding: 5px;
                .icons{
                    display: flex;
                    margin-bottom: 10px;
                    .icon{
                        border:2px solid #fff;
                        padding: 5px;
                        border-radius: 50%;
                        margin-right: 10px;
                        font-size: 16px;
                    }
                }
                .itemInfoTop{
                    display: flex;
                    align-items: center;
                    margin-bottom: 10px;
                    font-size: 14px;
                    font-weight: 500;
                    color: #555;
                    .limit{
                        border:1px solid #555;
                        padding: 1px 3px;
                        margin: 0 10px;
                    }
                }
                .desc{
                    font-size: 13px;
                    margin-bottom: 10px;
                }
                .genre{
                    font-size: 14px;
                    color: #d3d3d3;
                }
            }
        }
    }   
}

@media (hover:hover) and (pointer: coarse) {
    .listItem{
        &:hover{
            width: 225px;
            height: 120px;
            background-color: var(--main-color);
            margin-right: 5px;
            overflow:hidden;
            cursor:pointer;
            color: #fff;
            img{
                width:100%;
                height: 100%;
                object-fit: cover;
            }
            .video{
                width:100%;
                height:140px;
                object-fit: cover;
                position:absolute;
                top: 0;
                left: 0;
            }
        }
    }
}

